nav {
	position: fixed;
	width: 100%;
	padding: 1rem;
	top: 1rem;
}

nav img {
	width: 10rem;
	height: auto;
	cursor: pointer;
	object-fit: contain;
	position: absolute;
}

nav ul {
	visibility: hidden;
}

.hamburger-menu {
	cursor: pointer;
	position: absolute;
	right: 1rem;
	top: 1.4rem;
	display: flex;
	flex-direction: column;
	flex-wrap: nowrap;
	width: 3rem;
	height: 3rem;
	overflow: hidden;
	transform: rotateY(180deg) scale(0.8);
}

.hamburger-menu .burger {
	background-image: url("../../Design Assets/detail-header-bottom-black.svg");
	background-repeat: no-repeat;
	height: .5rem;
	background-position-x: -1rem;
	transform: scale(1.5);
	margin-top: 0.2rem;
}

.sm-links {
	position: fixed;
	justify-content: space-evenly;
	top: 78vh;
	left: 1rem;
	display: flex;
	flex-direction: column;
	width: 3.5rem;
	height: 8.5rem;
	background-color: #252525;
}

.sm-links div {
	background-repeat: no-repeat;
	background-size: contain;
	height: 1rem;
	width: 100%;
	background-position: center;
	cursor: pointer;
}

.sm-links div.twitter-link {
	background-image: url("../../Design Assets/icon-twitter.svg");
}

.sm-links div.insta-link {
	background-image: url("../../Design Assets/icon-instagram.svg");
}

.sm-links div.facebook-link {
	background-image: url("../../Design Assets/icon-facebook.svg");
}

.sm-links div.youtube-link {
	background-image: url("../../Design Assets/icon-youtube.svg");
}

.sm-links i {
	position: absolute;
	right: -2px;
	top: -2px;
	width: 0;
	height: 0;
	border-color: #fcee0a transparent transparent transparent;
	border-style: solid;
	border-width: 16px 0 0 16px;
}

* {
	box-sizing: border-box;
}

html {
	font-family: 'Saira', sans-serif;
}

header {
	height: 100vh;
}

.container-home {
	background: #fcee0a;
	height: 100vh;
	height: calc(var(--vh, 1vh) * 100);
	max-height: 1080px;
	width: 100%;
	display: grid;
	grid-template-rows: repeat(8, 1fr) 0.5fr;
	grid-template-columns: repeat(6, 1fr);
	overflow: hidden;
}

.container-home.animation-enter {
	display: absolute;
	top: 0;
	display: none;
}

.platform-select {
	grid-column: 1 / span 6;
	grid-row: 2/span 4;
	height: 6rem;
	display: flex;
	justify-content: center;
	position: relative;
	max-width: 100vw;
	overflow-x: hidden;
	overflow-y: visible;
}

.platform-select .platform {
	width: 8rem;
	display: flex;
	flex-direction: column-reverse;
	position: absolute;
	height: 100%;
	background-repeat: no-repeat;
	background-size: contain;
	background-position: center;
	background-position-y: 45%;
	transform: scale(0.9);
}

.platform-select .platform.ps4 {
	background-image: url("../../Design Assets/logo-ps4.svg");
}

.platform-select .platform.xbox {
	background-image: url("../../Design Assets/logo-xboxone.svg");
}

.platform-select .platform.pc {
	background-image: url("../../Design Assets/logo-pc.svg");
	background-size: 3rem;
}

.platform-select .platform.animation-enter {
	transform: translateX(50vw);
}

.platform-select .platform.animation-enter:before, .platform-select .platform.animation-enter:after {
	display: none;
}

.platform-select .platform.animation-exit {
	transform: translateX(-50vw);
}

.platform-select .platform.animation-exit:before, .platform-select .platform.animation-exit:after {
	display: none;
}

.platform-select .platform.animation-off-right {
	transform: translateX(100vw);
	visibility: hidden;
}

.platform-select .platform.animation-off-left {
	transform: translateX(-100vw);
	visibility: hidden;
}

.platform-select .platform:before, .platform-select .platform:after {
	content: "";
	position: absolute;
	height: 100%;
	width: 20px;
	top: 0px;
	background-image: radial-gradient(circle at center, #fe003c 1px, transparent 3px), radial-gradient(circle at center, #fe003c 1px, transparent 3px);
	padding: -0.25rem;
	background-size: 20px 20px;
	background-position: top center, bottom center;
	background-repeat: no-repeat;
}

.platform-select .platform:before {
	left: -1.5rem;
}

.platform-select .platform:after {
	right: -1.5rem;
}

.platform-select .platform-animation {
	position: absolute;
	bottom: 3px;
	height: 0.5rem;
	width: 7rem;
	transform: translateY(-7px) scaleY(0.7);
	overflow: hidden;
}

.platform-select .platform-animation:before, .platform-select .platform-animation:after {
	content: '';
	background-image: url("../../Design Assets/detail-header-bottom-red.svg");
	background-repeat: no-repeat;
	transform: scaleX(1.5);
	width: 4rem;
	height: 100%;
	position: absolute;
}

.platform-select .platform-animation:before {
	transform: rotateY(180deg) translateX(-8%) rotateX(180deg);
	background-position-x: -19%;
}

.platform-select .platform-animation:after {
	transform: translateX(71%) rotateX(180deg);
	background-position-x: -20%;
}

.platform-counter, .hero-counter {
	grid-column: 3 / span 2;
	height: 1rem;
	display: flex;
	justify-content: space-between;
}

.platform-counter.platform-counter, .hero-counter.platform-counter {
	grid-row: 3;
	margin-top: 1.25rem;
}

.platform-counter.hero-counter, .hero-counter.hero-counter {
	grid-row: 8;
	margin-top: -0.5rem;
}

.platform-counter button, .hero-counter button {
	border: none;
	outline: none;
	background-image: url("../../Design Assets/icon-arrow-select.svg");
	background-color: transparent;
	background-repeat: no-repeat;
}

.platform-counter button.previous-slide, .hero-counter button.previous-slide {
	transform: rotate(90deg);
}

.platform-counter button.next-slide, .hero-counter button.next-slide {
	transform: rotate(-90deg);
}

.platform-counter .slide-counter, .hero-counter .slide-counter {
	color: #fe003c;
	font-size: 0.875rem;
	text-shadow: 0px 0px 4px rgba(254, 0, 60, 0.5);
	font-weight: 700;
	opacity: 0.7;
}

.platform-counter .slash, .hero-counter .slash {
	margin: 0 0.5rem;
}

.hero-container {
	grid-column: 1 / span 7;
	grid-row: 3/7;
	margin-top: 3.75rem;
	height: 40vh;
	position: relative;
}

.hero-text {
	position: absolute;
	font-size: 8rem;
	white-space: nowrap;
	left: -1rem;
	top: 6rem;
}

.hero-text .zero, .hero-text .seven, .hero-text .seven-2 {
	opacity: 0.5;
	text-shadow: -1px -1px 0 #fcee0a, 1px -1px 0 #fcee0a, -1px 1px 0 #fcee0a, 1px 1px 0 #fcee0a;
}

.hero-text .two {
	z-index: 1;
}

.hero-text .zero {
	position: relative;
	z-index: 4;
}

.hero-text .seven {
	position: relative;
	z-index: 2;
}

.hero-text .seven-2 {
	position: relative;
	z-index: 4;
}

.trailer {
	overflow: hidden;
	margin-left: 4.5rem;
	height: 100%;
	position: relative;
	filter: brightness(0.8) hue-rotate(113deg);
}

.trailer img {
	object-fit: cover;
	height: 100%;
	top: 0;
	position: absolute;
}

.trailer .overlay {
	position: absolute;
	top: 0;
	left: 0;
	background-image: url("../../Design Assets/hero-overlay.png");
	mix-blend-mode: color-dodge;
	opacity: 1;
	background-size: 100%;
	height: 40rem;
	width: 100%;
}

.hero {
	position: absolute;
	z-index: 3;
	top: -32px;
	left: -13px;
	object-fit: contain;
	object-position: left;
	height: 110%;
	transform-origin: bottom;
	transform: scale(1.05);
}

.trailer-border:before, .trailer-border:after {
	content: '';
	height: 2rem;
	width: 10rem;
	background-image: url("../../Design Assets/detail-header-bottom-yellow.svg");
	position: absolute;
	background-repeat: no-repeat;
	background-size: 120%;
}

.trailer-border:before {
	left: 4.25rem;
	top: -2px;
	z-index: 2;
}

.trailer-border:after {
	transform: rotateZ(-90deg);
	z-index: 2;
	top: 2rem;
	left: .25rem;
}

.hero-container i {
	position: absolute;
	left: -1rem;
	bottom: -.25rem;
	width: 0;
	height: 0;
	border-color: transparent transparent transparent #fcee0a;
	border-style: solid;
	border-width: 60px 0 0 60px;
	z-index: 5;
	margin-left: 4.5rem;
}

.button-container-home {
	display: flex;
	position: absolute;
	grid-row: 5;
	bottom: 3rem;
	z-index: 101;
	justify-content: center;
	width: 100%;
	transform: translateX(-2%) scale(0.9);
}

.button-container-home button {
	outline: none;
	border: none;
	background-color: transparent;
	background-size: 100%;
	width: 11rem;
	background-repeat: no-repeat;
	font-size: .75rem;
	font-weight: 700;
	color: #fcee0a;
	text-transform: uppercase;
	height: 2.5rem;
	padding-top: 2px;
}

.button-container-home .pre-order {
	background-image: url("../../Design Assets/button-solid-red.svg");
	transform: translateX(10%);
	z-index: 2;
	padding-left: 15px;
}

.button-container-home .watch-trailer {
	background-image: url("../../Design Assets/button-outline-yellow.svg");
	transform: translateX(-10%) scaleX(1.1);
	z-index: 1;
	padding-left: 20px;
}

.next-hero-preview {
	background-image: url("../../Design Assets/hero-2-preview.png");
	height: 5rem;
	grid-column: 2/span 4;
	grid-row: 7;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	margin-top: -1.25rem;
	display: flex;
	flex-direction: column;
	justify-content: center;
	z-index: 101;
	text-align: center;
	padding-right: 5rem;
	font-size: 2rem;
	position: relative;
}

.scroll-indicator {
	grid-row-end: 9;
	grid-column: 3/5;
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;
}

.scroll-indicator div {
	background-color: #252525;
	margin-top: 0.5rem;
	height: 0.3rem;
	width: 0.3rem;
	border-radius: 5px;
}

.scroll-indicator div:first-child {
	opacity: 0.33;
}

.scroll-indicator div:nth-child(2) {
	opacity: 0.66;
}

.share-btn-mobile {
	grid-row-end: 9;
	grid-column: 6;
	background-image: url("../../Design Assets/share-button.svg");
	background-size: 40%;
	background-repeat: no-repeat;
	background-position-y: 100%;
	background-position-x: 100%;
	margin-right: 1rem;
}

.video-overlay {
	position: absolute;
	top: 0;
	left: 0;
	background-image: url("../../Design Assets/photoshop_scanlines_texture_overlay_by_sinner_pwa_d6uu408-pre.png");
	mix-blend-mode: color-burn;
	opacity: .5;
	background-size: 100%;
	height: 40rem;
	width: 100%;
}
